      2、根据法律、法规的要求、强制性的行政执法或司法要求所必须提供您信息的情况下，我们可能会依据所要求的信息类型和披露方式披露您的信息。在符合法律法规的前提下，当我们收到上述披露信息的请求时，我们会要求接收方必须出具与之相应的法律文件，如传票或调查函。我们坚信，对于要求我们提供的信息，应该在法律允许的范围内尽可能保持透明。我们对所有的请求都进行了慎重的审查，以确保其具备合法依据，且仅限于执法部门因特定调查目的且有合法权利获取的数据。
    </p>
    <p>
      四、我们如何保留、储存和保护信息
    </p>
    <p>
      我们仅在本《隐私政策》所述目的所必需期间和法律法规要求的时限内保留您的信息。
    </p>
    <p>
      我们在中华人民共和国境内运营中收集和产生的信息，存储在中国境内，以下情形除外：
    </p>
    <p>
      1、法律法规有明确规定；
    </p>
    <p>
      2、获得您的授权同意；
    </p>
    <p>
      3、您使用的产品、服务涉及跨境，需要向境外提供您的个人信息的。
    </p>
    <p>
      针对以上情形，我们会确保依据本政策及国家法律法规要求对您的个人信息提供足够的保护。
    </p>
    <p>
      我们将采取以下手段保护您的信息：
    </p>
    <p>
      （一）数据安全技术措施
    </p>
    <p>
      我们会采用符合业界标准的安全防护措施，包括建立合理的制度规范、安全技术来防止您的信息遭到未经授权的访问使用、修改,避免数据的损坏或丢失。网络服务采取了多种加密技术，例如在某些服务中，我们将利用加密技术（例如SSL）来保护您的信息，采取加密技术对您的信息进行加密保存，并通过隔离技术进行隔离。
    </p>
    <p>
      在信息使用时，例如信息展示、信息关联计算，我们会采用多种数据脱敏技术增强信息在使用中安全性。采用严格的数据访问权限控制和多重身份认证技术保护信息，避免数据被违规使用。
    </p>
    <p>
      （二）我们为保护信息采取的其他安全措施
    </p>
    <p>
      我们通过建立数据分类分级制度、数据安全管理规范、数据安全开发规范来管理规范信息的存储和使用。
    </p>
    <p>
      我们通过信息接触者保密协议、监控和审计机制来对数据进行全面安全控制。
    </p>
    <p>
      加强安全意识。我们还会举办安全和隐私保护培训课程，加强员工对于保护信息重要性的认识。
    </p>
    <p>
      （三）我们仅允许有必要知晓这些信息的员工、合作伙伴访问您的信息，并为此设置了严格的访问权限控制和监控机制。我们同时要求可能接触到您的信息的所有人员履行相应的保密义务。如果未能履行这些义务，可能会被追究法律责任或被中止与的合作关系。
    </p>
    <p>
      （四）我们会采取一切合理可行的措施，确保未收集无关的信息。
    </p>
    <p>
      （五）互联网并非绝对安全的环境，而且电子邮件、即时通讯、社交软件或其他服务软件等与其他用户的交流方式无法确定是否完全加密，我们建议您使用此类工具时请使用复杂密码，并注意保护您的信息安全。
    </p>
    <p>
      （六）互联网环境并非百分之百安全，我们将尽力确保或担保您发送给我们的任何信息的安全性。如果我们的物理、技术、或管理防护设施遭到破坏，导致信息被非授权访问、公开披露、篡改、或毁坏，导致您的合法权益受损，我们将承担相应的法律责任。
    </p>
    <p>
      （七）安全事件处置
    </p>
    <p>
      在通过服务与第三方进行沟通或购买商品及服务时，您不可避免的要向交易对方或潜在的交易对方披露自己的信息，如联络方式或者邮政地址等。请您妥善保护自己的信息，仅在必要的情形下向他人提供。
    </p>
    <p>
      为应对信息泄露、损毁和丢失等可能出现的风险，我们制定了多项制度，明确安全事件、安全漏洞的分类分级标准及相应的处理流程。我们也为安全事件建立了专门的应急响应团队，按照安全事件处置规范要求，针对不同安全事件启动安全预案，进行止损、分析、定位、制定补救措施、联合相关部门进行溯源和打击。
    </p>
    <p>
      在不幸发生信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们同时将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，主动上报信息安全事件的处置情况。
    </p>
    <p>
      请您理解，由于技术的限制以及风险防范的局限，即便我们已经尽量加强安全措施，也无法始终保证信息百分之百的安全。您需要了解，您接入服务所用的系统和通讯网络，有可能因我们可控范围外的情况而发生问题。
    </p>
    <p>
      五、如何管理您的信息
    </p>
    <p>
      （一）访问、更新和删除
    </p>
    <p>
      我们鼓励您更新和修改您的信息以使其更准确有效。您能通过服务访问您的信息，并根据对应信息的管理方式自行完成或要求我们进行修改、补充和删除。我们将采取适当的技术手段，尽可能保证您可以访问、更新和更正自己的信息或使用服务时提供的其他信息。
    </p>
    <p>
      在访问、更新、更正和删除前述信息时，我们可能会要求您进行身份验证，以保障信息安全。对于通过COOKIES和WEB
      BEACON收集的您的信息，我们还在本《隐私政策》第二条“COOKIES、日志档案和WEB BEACON”部分说明了向您提供的选择机制。
    </p>
    <p>
      （二）公开与分享
    </p>
    <p>
      我们的多项服务可让您不仅与您的社交网络、也与使用该服务的所有用户公开分享您的相关信息，例如，您在服务中所上传或发布的信息、您对其他人上传或发布的信息作出的回应，通过电子邮件或在服务中不特定用户可见的公开区域内上传或公布您的个人信息，以及包括与这些信息有关的位置数据和日志信息。只要您不删除您所公开或共享的信息，有关信息可能一直留存在公众领域；即使您删除共享信息，有关信息仍可能由其他用户或不受我们控制的第三方独立地缓存、复制或储存，或由其他用户或该等第三方在公众领域保存。如您将信息通过上述渠道公开或共享，由此造成您的信息泄露，我们不承担责任。因此，我们提醒并请您慎重考虑是否通过上述渠道公开或共享您的信息。
    </p>
    <p>
      （三）注销
    </p>
    <p>
      在符合单项服务的服务协议约定条件及国家相关法律法规规定的情况下，您的该项服务帐号可能被注销或删除。当帐号注销或被删除后，与该帐号相关的、该单项服务项下的全部服务资料和数据将依照单项服务的服务协议约定删除或处理。
    </p>
    <p>
      （四）改变您授权同意的范围
    </p>
    <p>
      您总是可以选择是否披露信息。有些信息是使用服务所必需的，但大多数其他信息的提供是由您决定的。您可以通过删除信息、关闭设备功能等方式改变您授权我们继续收集信息的范围或撤回您的授权。
    </p>
    <p>
      当您撤回授权后，我们无法继续为您提供撤回授权所对应的服务，也不再处理您相应的信息。但您撤回授权的决定，不会影响此前基于您的授权而开展的信息处理。
    </p>
    <p>
      （五）有关敏感信息的提示
    </p>
    <p>
      某些信息因其特殊性可能被认为是敏感信息，例如您的种族、宗教、个人健康和医疗信息等，以及身份证明文件、个人生物识别信息、财产信息、行踪轨迹、未成年人的信息等。
    </p>
    <p>
      请注意，您在服务中所提供、上传或发布的内容和信息（例如有关您社交活动的照片或信息），可能会泄露您的敏感信息。您需要谨慎地考虑，是否使用服务披露您的敏感信息。
    </p>
    <p>
      您同意您的敏感信息按本《隐私政策》所述的目的和方式来处理。
    </p>
    <p>
      六、第三方服务
    </p>
    <p>
      服务可能链接至第三方提供的社交媒体或其他服务（包括网站或其他服务形式）。包括：
    </p>
    <p>
      （1）您可利用“分享”键将某些内容分享到服务，或您可利用第三方服务登录服务。这些功能可能会收集您的信息（包括您的日志信息），并可能在您的电脑装置COOKIES，从而正常运行上述功能；
    </p>
    <p>
      （2）我们通过广告或我们服务的其他方式向您提供链接，使您可以接入第三方的服务或网站；及
    </p>
    <p>
      （3）其他接入第三方服务的情形。
    </p>
    <p>
      该等第三方社交媒体或其他服务由相关的第三方负责运营。您使用该等第三方的社交媒体服务或其他服务（包括您向该等第三方提供的任何信息），须受第三方自己的服务条款及信息保护声明（而非本《隐私政策》）约束，您需要仔细阅读其条款。本《隐私政策》仅适用于我们所收集的任何信息，并不适用于任何第三方提供的服务或第三方的信息使用规则，而我们对任何第三方使用由您提供的信息不承担任何责任。
    </p>
    <p>
      七、年龄限制
    </p>
    <p>
      我们建议：任何未成年人参加网上活动应事先取得家长或其法定监护人（以下简称&quot;监护人&quot;）的同意。我们将根据国家相关法律法规的规定保护未成年人的相关信息。
    </p>
    <p>
      我们鼓励父母或监护人指导未成年人使用服务。我们建议未成年人鼓励他们的父母或监护人阅读本《隐私政策》，并建议未成年人在提交信息之前寻求父母或监护人的同意和指导。
    </p>
    <p>
      八、通知和修订
    </p>
    <p>
      我们可能适时修改本《隐私政策》的条款，该等修改构成本《隐私政策》的一部分。对于重大变更，我们会提供更显著的通知，您可以选择停止使用服务；在该种情况下，如您仍然继续使用服务的，即表示同意受经修订的本《隐私政策》的约束。
    </p>
    <p>
      任何修改都会将您的满意度置于首位。我们鼓励您在每次使用服务时都查阅我们的隐私政策。
    </p>
    <p>
      我们可能在必需时（例如当我们由于系统维护而暂停某一项服务时）发出与服务有关的公告。您可能无法取消这些与服务有关、性质不属于推广的公告。
    </p>
    <p>
      最后，您必须对您的账号和密码信息负有保密义务。任何情况下，请小心妥善保管。
    </p>
    <p>
      九、如何联系我们
    </p>
    <p>
      如您有关于网络信息安全的投诉和举报，或您对本《隐私政策》、您的信息的相关事宜有任何问题、意见或建议，以及有关本声明或的隐私措施的问题请与的协调人联系。
    </p>
    <p>
      附件一：
    </p>
