    </p>
    <p>
      支付隐私政策
    </p>
    <p>
      重要提示：
    </p>
    <p>
      支付在此特别提醒用户认真阅读、充分理解本政策各条款，特别是其中所涉及的免除支付责任或限制支付功能的条款、对用户权利限制条款、管辖与法律适用条款等。为了特别提示您限制、免责条款以及涉及到您的权益的等内容，这些条款可能以字体加粗形式提示您注意。
    </p>
    <p>
      为了更好的保护您的信息并告知您的信息收集、使用情况，我们制定了支付隐私政策，请您审慎阅读并选择接受或不接受本政策（未成年人应在监护人陪同下阅读）。当您同意《支付服务协议》时，即表示您已同意我们按照本政策收集、储存、使用和分享您的信息。除非您已阅读并接受本政策所有条款，否则您无权使用支付服务。如您遇到任何障碍或疑问，可联系本页面的客服联系邮箱。
    </p>
    <p>
      （一）用户个人信息的收集
    </p>
    <p>
      为了您可以正常使用支付服务，在下列情形下您需要向我们提供一些信息，我们也会在下列情形下向合法存有您信息的第三方收集您的信息。
    </p>
    <p>
      1、依据法律法规及监管规定履行反洗钱义务及进行实名制管理。
    </p>
    <p>
      (1)在您注册支付账户并使用支付服务时，按照反洗钱的相关规定，您需要提供您的姓名、国籍、性别、职业、住址、联系方式以及客户有效身份证件的种类、号码和有效期限。如您不提供上述信息，将无法注册账户或无法支付。同时为了验证该类信息的准确性和完整性，我们会将您提供的信息与合法存有您信息的机构（政府机关、事业单位、商业机构）进行验证核对。
    </p>
    <p>
      2、支付
    </p>
    <p>
      (1)为了您能使用快捷支付服务，在您开通快捷支付服务，将您的银行卡与支付账户绑定时，您需提供您的银行卡卡号、姓名、身份证号码、银行预留手机号。我们会将该等信息与发卡银行进行验证。如您不提供上述信息，将无法使用快捷支付服务。
    </p>
    <p>
      (2)当您向商家付款时，为了让您完成交易、实现交易目的，商家可在付款过程中，或付款成功后查看您使用的支付账户登录名、账户对应的姓名或昵称及该笔交易信息等。同时，商家会将您的支付金额、支付对象传输给我们以确认是否是您的真实意思表示。按照法律法规及监管规定，我们需要对商家进行管理，如识别、防止其套现或欺诈，商家会将购买商品、收货地址、联系方式提供给我们。如您不同意商家提供该等信息，则无法完成付款。
    </p>
    <p>
      (3)为了帮助您更安全、快速地登录、认证支付指令及使用支付提供的其他功能，支付为您提供人脸识别服务。当您选择我们提供的人脸识别服务时，需要您向我们提供您的人脸照片或人脸视频。
    </p>
    <p>
      （4）为了帮助您更安全、快捷的登录及认证支付指令，您可选择指纹解锁服务及指纹支付服务。在您进行指纹解锁或支付时，需要使用您的指纹信息进行验证，我们不会采集您的指纹原图，您的指纹原图仅保存在您授权采集指纹的设备上。如您不想提供指纹信息，仍可通过输入密码方式进行支付。
    </p>
    <p>
      （5）为保护您账户的安全，当您需要找回您的密码时，我们需要验证您的身份信息，您需要向我们提供符合要求的身份信息。
    </p>
    <p>
      3、投诉处理
    </p>
    <p>
      为了保护您及他人的合法权益，维护绿色购物及合法网络支付交易环境，如您被他人投诉或投诉他人，我们会将您的姓名及身份证号码、投诉相关内容提供给消费者权益保护部门及相应监管机关，以便及时解决投诉纠纷；但法律法规明确禁止提供的除外。
    </p>
    <p>
      4、反欺诈及其他风险防范
    </p>
    <p>
      为了防止您的资金、个人信息被不法分子获取并侵害您的合法权益，我们需要收集您的下列信息以判断您账户的风险：当您使用支付服务时，我们可能会记录您使用的服务类别和方式、使用服务时的操作信息，如您的IP地址、移动设备所使用的版本和设备识别码、设备标识符、您的位置以及其他与支付服务相关的日志信息。为更准确地预防钓鱼网站、欺诈和木马病毒，我们可能会通过了解一些您的网络使用习惯、您常用的软件信息来判断风险，并可能会记录一些我们认为有风险的URL、APP或软件。您可以设置关闭收集位置功能，这样我们会停止对您的位置信息的收集，这样做不会影响您进行支付，但可能会影响我们判断是否安全的效果。
    </p>
    <p>
      5、为了提供个性化服务及改进服务质量
    </p>
    <p>
      为提升您的服务体验及改进服务质量，或为防范风险，或者为您推荐更优质或更适合的服务，我们会收集您与客户服务团队联系时提供的信息，收集您参与问卷调查时向我们发送的问卷答复信息，及您使用的服务类别和方式、使用服务时的操作信息以及有关您曾使用的移动应用（APP）和其他软件的信息；还会向支付关联公司和合作伙伴收集其合法留存的您的行为信息、交易信息。我们会对这些信息进行统计、分析，以为您提供更加准确、个性、流畅及便捷的服务。
    </p>
    <p>
      6、为了提供服务状态通知及查询服务
    </p>
    <p>
      为了遵守法律法规及监管规定，也为了便于您查询交易状态或历史记录，我们会将您使用支付服务时的身份信息、交易信息、行为信息进行存档，并严格按照法律法规的规定对这些信息进行妥善保管。为了使您知晓使用支付服务的状态，我们会根据这些信息向您发送服务状态通知。
    </p>
