    </p>
    <p>
      (2)与公共安全、公共卫生、重大公共利益有关的；
    </p>
    <p>
      (3)与犯罪侦查、起诉、审判和判决执行等有关的；
    </p>
    <p>
      (4)所收集的个人信息是您自行向社会公众公开的；
    </p>
    <p>
      (5)从合法公开披露的信息中收集个人信息，如合法的新闻报道、政府信息公开等渠道；
    </p>
    <p>
      (6)用于维护服务的安全和合规所必需的，例如发现、处置产品和服务的故障。
    </p>
    <p>
      （二）我们如何共享、转让和公开披露个人信息
    </p>
    <p>
      1、共享
    </p>
    <p>
      我们承诺对您的信息进行保密。除本政策及法律法规另有规定外，仅在下列情形下与支付关联公司、我们的供应商或合作伙伴共享您的信息。我们的供应商包括提供技术服务、电信服务、客户服务、风控服务、审计服务、法律服务的机构。合作伙伴包括金融机构、电子商务公司及平台、合作商户。以下将支付关联公司、我们的供应商及合作伙伴合称“第三方”。在和第三方合作前，我们会尽商业上的合理努力对该第三方的安全能力进行检测，评估其采集信息的正当合法必要性，并与第三方签订保密协议，对其查询行为进行技术监测，尽商业上的合理努力督促第三方在使用您的个人信息时遵守法律法规及协议中约定的保密和安全措施。一经发现其违反协议约定将会采取有效措施乃至终止合作：
    </p>
    <p>
      1）在获取您同意的情况下与第三方共享您的信息。
    </p>
    <p>
      2）只有共享您的信息，才能提供您需要的服务，或处理您与他人的交易纠纷或争议。例如，与您交易的相对方或您本人否认交易为本人操作时，我们可能会将您提交的发货凭证、签收单据，或交易信息分享给银行或您的交易相对方，以证明交易的真实存在，维护您的合法权益。
    </p>
    <p>
      3）如您参与抽奖、竞赛或类似推广活动，我们会将您提供的信息用于管理此类活动，如您中奖，可能会将您的手机号或支付登录名脱敏后以公布中奖名单。若该活动是我们和第三方联合推广的，我们可能与其共享活动过程中产生的、为完成活动所必要的个人信息（参加活动的用户数、中奖名单、中奖人联系方式），以便第三方能及时向您发放奖品或为您提供服务。
    </p>
    <p>
      4）为遵守适用的法律法规及监管规定、维护社会公共利益，或保护我们的其他用户、支付或支付员工的人身和财产安全或合法权益所合理必需的用途。
    </p>
    <p>
      5）根据法律规定及合理商业习惯，在我们计划与其他公司合并或被其收购或进行其他资本市场活动（包括但不限于IPO，债券发行）时，我们需要接受来自其他主体的尽职调查时，我们会把您的信息提供给必要的主体，但我们会通过和这些主体签署保密协议等方式要求其对您的个人信息采取合理的保密措施。
    </p>
    <p>
      6）如您授权第三方向我们查询、采集您在支付的信息，则我们会在法律法规允许范围内及您对第三方的授权范围内向第三方共享您的信息。
    </p>
    <p>
      7）根据国家相关外汇监管法律法规及跨境电子商务政策，当您使用“支付”在跨境电子商务平台（包括但不限于考拉海购）进行跨境电子商务交易时，您授权并同意“支付”将您的支付信息及支付账户实名认证信息（包括姓名及身份证号码等）发送给跨境电子商务平台，以便跨境电子商务平台及“支付”为您提供代理购结汇服务，及向海关发送订购人信息用于清关。因此您还需同意该跨境电子商务平台需要您确认的相关协议，并承担由此所产生的法律后果。如需向境外相关第三方提供个人信息才能完成该交易的，经您授权后，且在符合法律、行政法规及监管规定的前提下，我们会向境外第三方提供该信息。该相关第三方所在地区可能有不同的个人信息保护法，我们将与该第三方签署保密协议，并将信息进行加密传输，尽商业上合理努力督促第三方在使用您的个人信息时遵守其适用的法律法规及协议约定的保密和安全措施。
    </p>
    <p>
      2、转让
    </p>
    <p>
      我们不会将您的个人信息转让给任何公司、组织和个人，但以下情况除外：
    </p>
    <p>
      1）事先获得您明确的同意或授权；
    </p>
    <p>
      2）在涉及合并、收购或破产清算时，如涉及到个人信息转让，我们会要求新的持有您个人信息的公司、组织继续受本政策的约束，否则我们将要求该公司、组织重新向您征求授权同意。
    </p>
    <p>
      3、披露
    </p>
    <p>
      我们仅会在以下情况下，且采取符合业界标准的安全防护措施的前提下，才会公开披露您的个人信息：
    </p>
    <p>
      1、根据您的需求，在您明确同意的披露方式下披露您所指定的个人信息；
    </p>
    <p>
      2、根据法律、法规的要求、强制性的行政执法或司法要求所必须提供您个人信息的情况下，我们可能会依据所要求的个人信息类型和披露方式公开披露您的个人信息。在符合法律法规的前提下，当我们收到上述披露信息的请求时，我们会要求必须出具与之相应的法律文件，如传票或调查函。我们坚信，对于要求我们提供的信息，应该在法律允许的范围内尽可能保持透明。我们对所有的请求都进行了慎重的审查，以确保其具备合法依据，且仅限于执法部门因特定调查目的且有合法权利获取的数据。
    </p>
    <p>
      （三）您如何访问和控制自己的个人信息
    </p>
    <p>
      1、您可随时登录您的支付账户查询并管理该账户下您的个人信息，如您遇到任何障碍或疑问，可登录支付客服专区获取相关信息或拨打宝公司客服电话（400-0881188）或与在线客服联系。但出于安全性和身份识别的考虑或根据法律法规的强制规定，您可能无法修改注册时提供的初始注册信息。
    </p>
    <p>
