    </p>
    <p>
      3、您可在登录支付账户后，在账户管理中管理或解绑您绑定的银行卡。
    </p>
    <p>
      4、您可根据支付服务协议第四条“注销”条款申请注销支付账户。
    </p>
    <p>
      您注销支付账户后，您该账户内的所有信息将被清空，我们将不会再收集、使用或共享与该账户相关的个人信息，但因您使用支付服务所留存的身份信息和支付业务信息，我们仍需按照监管要求的时间进行保存，且在该依法保存的时间内有权机关仍有权依法查询。为保护信息的安全性，支付制定了严格的账户及交易信息保护管理制度，定期对系统进行评测工作，并接受监管机关的监督与管理。
    </p>
    <p>
      5、如您发现我们采集、使用您个人信息的行为，违反了法律、行政法规规定或违反了与您的约定，您可联系我们的客服邮箱，要求删除该违反行为下采集的您的个人信息。
    </p>
    <p>
      6、如您发现我们采集、储存的您的个人信息有错误的，您可以联系我们的客服邮箱要求更正；
    </p>
    <p>
      7、在响应您上述5、6项要求前，为保障安全，我们需要您提供相关身份证明材料，验证通过后，我们将严格按照您的要求进行处理并反馈结果。
    </p>
    <p>
      8、尽管有上述约定，但按照法律法规要求，在以下情形下，我们可能无法响应您的请求：
    </p>
    <p>
      (1)与国家安全、国防安全有关的；
    </p>
    <p>
      (2)与公共安全、公共卫生、重大公共利益有关的；
